import React from 'react'
import { useAuth0 } from "@auth0/auth0-react";


export default function Swapi(props) {
  const { isAuthenticated } = useAuth0()
  return (
    isAuthenticated && (

<div className='api-list'>
        { props.swapi.map(p=>(
            <div key={p}>{p}
            </div>))}
</div>
        ) ) }

    